.client-data-list {
  display: grid;
  grid-template-rows: 1fr 1fr 1fr;
}

.client-data-list__item {
  display: grid;
  grid-template-columns: auto 1fr;
  align-items: center;
  margin-bottom: 13px;
  gap: 15px;

  @media (min-width: 768px) {
    margin-bottom: 20px;
    gap: 25px;
  }

